.landingPage {
  &__main {
    width: 100%;
    height: 830px;
    position: relative;
  }
  &__image {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
  &__form {
    position: absolute;
    left: 9.7%;
    top: 8.4%;
    @media screen and (max-width: 576px) {
      left: 50%;
      transform: translate(-50%, 0);
      width: fit-content;
      padding-left: 20px;
      padding-right: 20px;
    }
  }
  &__text {
    position: absolute;
    right: 9.3%;
    bottom: 2.5%;
    @extend %font-body;
    line-height: 24px;
    width: 260px;
    text-align: end;
    color: $dark75;
  }
}
